import React, { useState, useEffect } from 'react';
import Hero from './Hero/Hero';
import About from './About/About';
import Works from './Work/Work';
import Research from './Research/Research';
import Education from './Education/Education';
import Contact from './Contact/Contact';
import Footer from './Footer/Footer';

import { PortfolioProvider } from '../context/context';

import { heroData, contactData, footerData } from '../mock/data';
import { aboutData, worksData, researchData, educationData } from '../live/data';

function App() {
  const [hero, setHero] = useState({});
  const [about, setAbout] = useState({});
  const [works, setWorks] = useState([]);
  const [research, setResearch] = useState({});
  const [education, setEducation] = useState({});
  const [contact, setContact] = useState({});
  const [footer, setFooter] = useState({});

  useEffect(() => {
    setHero({ ...heroData });
    setAbout({ ...aboutData });
    setWorks([...worksData]);
    setResearch({ ...researchData });
    setEducation({ ...educationData });
    setContact({ ...contactData });
    setFooter({ ...footerData });
  }, []);

  return (
    <PortfolioProvider value={{ hero, about, works, research, education, contact, footer }}>
      <Hero />
      <About />
      <Works />
      <Research />
      <Education />
      <Contact />
      <Footer />
    </PortfolioProvider>
  );
}

export default App;
